import {
  USER_LOGIN_FAILED,
  USER_LOGIN_REQUEST,
  USER_LOGIN_SUCCESS,
  USER_LOGOUT,
  USER_REGISTER_FAILED,
  USER_REGISTER_REQUEST,
  USER_REGISTER_SUCCESS,
  USER_DETAILS_FAILED,
  USER_DETAILS_REQUEST,
  USER_DETAILS_SUCCESS,
  USER_UPDATE_PROFILE_REQUEST,
  USER_UPDATE_PROFILE_SUCCESS,
  USER_UPDATE_PROFILE_FAILED,
  USER_UPDATE_PROFILE_RESET,
  USER_DETAILS_RESET,
  USER_LIST_BA_SUCCESS,
  USER_LIST_BA_REQUEST,
  USER_LIST_BA_FAILED,
  USER_LIST_BA_RESET,
  USER_DELETE_BA_REQUEST,
  USER_DELETE_BA_SUCCESS,
  USER_DELETE_BA_FAILED,
  USER_UPDATE_BA_REQUEST,
  USER_UPDATE_BA_SUCCESS,
  USER_UPDATE_BA_FAILED,
  USER_UPDATE_BA_RESET,
} from '../constants/allConstants'

export const userLoginReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_LOGIN_REQUEST:
      return { loading: true }
    case USER_LOGIN_SUCCESS:
      return { loading: false, userInfo: action.payload }
    case USER_LOGIN_FAILED:
      return { loading: false, error: action.payload }
    case USER_LOGOUT:
      return {}

    default:
      return state
  }
}

export const userRegisterReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_REGISTER_REQUEST:
      return { loading: true }
    case USER_REGISTER_SUCCESS:
      return { loading: false, userInfo: action.payload }
    case USER_REGISTER_FAILED:
      return { loading: false, error: action.payload }
    default:
      return state
  }
}

export const userDetailsReducer = (state = { user: {} }, action) => {
  switch (action.type) {
    case USER_DETAILS_REQUEST:
      return { ...state, loading: true }
    case USER_DETAILS_SUCCESS:
      return { loading: false, user: action.payload }
    case USER_DETAILS_FAILED:
      return { loading: false, error: action.payload }
    case USER_DETAILS_RESET:
      return { user: {} }

    default:
      return state
  }
}

export const userUpdateProfileReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_UPDATE_PROFILE_REQUEST:
      return { loading: true }
    case USER_UPDATE_PROFILE_SUCCESS:
      return { loading: false, success: true, userInfo: action.payload }
    case USER_UPDATE_PROFILE_FAILED:
      return { loading: false, error: action.payload }
    case USER_UPDATE_PROFILE_RESET:
      return {}
    default:
      return state
  }
}

export const userListByAdminReducer = (state = { users: [] }, action) => {
  switch (action.type) {
    case USER_LIST_BA_REQUEST:
      return { loading: true }
    case USER_LIST_BA_SUCCESS:
      return { loading: false, users: action.payload }
    case USER_LIST_BA_FAILED:
      return { loading: false, error: action.payload }
    case USER_LIST_BA_RESET:
      return { users: [] }

    default:
      return state
  }
}

export const userDeleteByAdminReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_DELETE_BA_REQUEST:
      return { loading: true }
    case USER_DELETE_BA_SUCCESS:
      return { loading: false, success: true }
    case USER_DELETE_BA_FAILED:
      return { loading: false, error: action.payload }
    default:
      return state
  }
}

export const userUpdateByAdminReducer = (state = { user: {} }, action) => {
  switch (action.type) {
    case USER_UPDATE_BA_REQUEST:
      return { loading: true }
    case USER_UPDATE_BA_SUCCESS:
      return { loading: false, success: true }
    case USER_UPDATE_BA_FAILED:
      return { loading: false, error: action.payload }
    case USER_UPDATE_BA_RESET:
      return { user: {} }

    default:
      return state
  }
}
